<template>
  <div class="excel-container">
    <el-card class="upload-box">
      <el-upload
        class="upload-style"
        ref="uploadRef"
        :action="`${apiUrl}/v1/excel/index/index?token=theposter`"
        :http-request="httpRequest"
        :on-preview="handlePreview"
        :on-remove="handleRemove"
        :before-remove="beforeRemove"
        :file-list="fileList"
        :on-success="onSuccess"
        :on-error="onError"
      >
        <el-button class="import-excel-btn-style" size="small" type="primary"
          >点击导入Excel</el-button
        >
        <div slot="tip" class="el-upload__tip">
          只能上传jpg/png文件，且不超过500kb
        </div>
      </el-upload>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 当前环境
      environment: null,

      // 接口域名
      apiUrl: process.env.VUE_APP_API_URL,

      /**
       * 上传的文件列表, 例如: [{name: 'food.jpg', url: 'https://xxx.cdn.com/xxx.jpg'}]
       */
      fileList: [],
    };
  },
  created() {
    this.environment = this.$route.params.environment;
  },
  methods: {
    /**
     * 上传文件之前的钩子，参数为上传的文件，若返回 false 或者返回 Promise 且被 reject，则停止上传。
     */
    async httpRequest(params) {
      console.log("params :>> ", params);
      const formData = new FormData();
      formData.append("file", params.file);
      console.log("formData :>> ", formData);
      console.log("this.$request :>> ", this.$request);

      const res = await this.$request("excel.uploadExcel", formData, {
        checkRes: false,
      });
      if (res.code && res.code !== 0) {
        params.onError(res.echo);
      }
      params.onSuccess();
      console.log("Excel文件上传res :>> ", res);
      // 路由环境参数值是否等于"讲座"
      if (this.environment === "lecture") {
        // 调用讲座批量上传接口
        this.lectureUploadApi(res);
        return;
      }
      // 调用教师批量上传
      this.instructorUploadApi(res);
    },
    onSuccess(response, file, fileList) {
      // this.$message.success("恭喜,上传成功!");
    },
    onError(err, file, fileList) {
      this.$message.error("上传失败!");
    },

    /**
     * 讲座批量上传
     */
    async lectureUploadApi(params) {
      const data = {
        json: JSON.stringify(params),
      };
      const res = await this.$request("excel.lectureUpload", data);
      console.log("讲座批量上传res :>> ", res);

      if (res.code && res.code !== 0) {
        this.$confirm(
          `data:>>${JSON.stringify(res.data)}______echo:>>${res.echo}`
        )
          .then((_) => {
            done();
          })
          .catch((_) => {});
      }
    },
    /**
     * 教师批量上传
     */
    async instructorUploadApi(params) {
      const data = {
        json: JSON.stringify(params),
      };
      const res = await this.$request("excel.instructorUpload", data);
      console.log("教师批量上传res :>> ", res);

      if (res.code && res.code !== 0) {
        this.$confirm(
          `data:>>${JSON.stringify(res.data)}______echo:>>${res.echo}`
        )
          .then((_) => {
            done();
          })
          .catch((_) => {});
      }
    },
    /**
     * 文件列表移除文件时的钩子
     */
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    /**
     * 点击文件列表中已上传的文件时的钩子
     */
    handlePreview(file) {
      console.log(file);
    },
    /**
     * 删除文件之前的钩子，参数为上传的文件和文件列表，若返回 false 或者返回 Promise 且被 reject，则停止删除。
     */
    beforeRemove(file, fileList) {
      return this.$confirm(`确定移除 ${file.name}？`);
    },
  },
};
</script>

<style lang="scss" scoped>
.excel-container {
  padding: px(24);
  .upload-box {
    margin: 0 auto;
    width: px(400);
    .upload-style {
      ::v-deep .el-upload {
        width: 100%;
        text-align: center;
      }
      .import-excel-btn-style {
        margin-bottom: px(34);
        width: px(300);
        height: px(300);
        border-radius: 50%;
        font-size: px(40);

        box-shadow: px(5) px(5) px(5) #ccc;
      }
    }
  }
}
</style>
